<template>
<section class="hero is-dark is-fullheight is-bold" id="index">
  <div class="hero-body">
    <div class="container has-text-centered">
      <h1 class="title is-size-1 is-size-3-mobile">
        T U R B U L E N S S I
      </h1>
      <div class="columns is-centered">
        <div class="column is-narrow">
          <h2 class="title is-size-1">
            1961
          </h2>
        </div>
        <div class="column is-narrow">
          <figure class="image container is-48x48">
              <img src="@/assets/valkoinen_nuoli.png">
          </figure>
        </div>
        <div class="column is-narrow">
          <h2 class="title is-size-1">
            2021
          </h2>
        </div>
      </div>
      <p id="timer"></p>
    </div>
  </div>
</section>
</template>

<script>
  export default {
      name: 'Index',
  }
  var countDownDate = new Date("Oct 10, 2021 00:00:00").getTime();
  var x = setInterval(function() {

    var now = new Date().getTime();

    var distance = countDownDate - now;

    var days = Math.floor(distance / (1000 * 60 * 60 * 24));
    var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    var seconds = Math.floor((distance % (1000 * 60)) / 1000);

    document.getElementById("timer").innerHTML = days + " päivää " + hours + " tuntia "
    + minutes + " minuuttia ja " + seconds + " sekunttia ";

    if (distance < 0) {
      clearInterval(x);
      document.getElementById("timer").innerHTML = "EXPIRED";
    }
  }, 100);
</script>

